tainer {\r\n  display: flex;\r\n  justify-content: center;\r\n  height: 6rem;\r\n  width: auto;\r\n  margin-top: 10rem;\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.logo__image {\r\n  display: inline-flex;\r\n}\r\n\r\n/* Body Grid */\r\n.wrap {\r\n  display: flex;\r\n  justify-content: center;\r\n}\r\n\r\n/* Grid Gaming Area */\r\n\r\n.container {\r\n  display: grid;\r\n  cursor: default;\r\n\r\n  grid-template-columns: 0.6fr 1.4fr 1.4fr 0.6fr;\r\n  grid-template-rows: 0.3fr 1.4fr 1.5fr 0.2fr 0.3fr;\r\n  gap: 0px 0px;\r\n  grid-auto-flow: row;\r\n  margin: 0;\r\n  grid-template-areas:\r\n    \". attack1 attack2 .\"\r\n    \"level player1 player2 btn1\"\r\n    \"time player1 player2 btn2\"\r\n    \". attacks-left1 attacks-left2 .\"\r\n    \". score1 score2 .\";\r\n}\r\n\r\n.player1 {\r\n  justify-self: center;\r\n  grid-area: player1;\r\n  width: 28rem;\r\n  height: 45rem;\r\n  background: var(--color-main);\r\n  margin-left: 4rem;\r\n  margin-right: 3rem;\r\n}\r\n\r\n.fa-exclamation {\r\n  position: absolute;\r\n  color: var(--color-piece3);\r\n  top: 17rem;\r\n  left: 38rem;\r\n}\r\n\r\n.player2 {\r\n  justify-self: center;\r\n  grid-area: player2;\r\n  width: 28rem;\r\n  height: 45rem;\r\n  background: var(--color-main);\r\n  margin-left: 3rem;\r\n  margin-right: 4rem;\r\n}\r\n\r\n.attack1 {\r\n  justify-self: center;\r\n  align-self: end;\r\n  grid-area: attack1;\r\n}\r\n\r\n.attack2 {\r\n  justify-self: center;\r\n  align-self: end;\r\n  grid-area: attack2;\r\n}\r\n\r\n.level {\r\n  justify-self: end;\r\n  align-self: start;\r\n  grid-area: level;\r\n}\r\n\r\n.level h2 {\r\n  margin: 0;\r\n}\r\n\r\n.level__current-level {\r\n  color: var(--color-piece5);\r\n}\r\n\r\n.time {\r\n  justify-self: end;\r\n  align-self: end;\r\n  grid-area: time;\r\n}\r\n\r\n.time h2 {\r\n  margin: 0;\r\n}\r\n\r\n.time__current-time {\r\n  color: var(--color-piece6);\r\n}\r\n\r\n.btn1 {\r\n  justify-self: start;\r\n  align-self: end;\r\n  grid-area: btn1;\r\n  cursor: pointer;\r\n}\r\n\r\n.btn1__play {\r\n  display: flex;\r\n  justify-content: left;\r\n  align-items: center;\r\n}\r\n\r\n.btn1__play h3 {\r\n  margin: 1.5rem 1.5rem 1.5rem 1.5rem;\r\n}\r\n\r\n.btn1__play:hover .fa-play {\r\n  color: var(--color-piece1);\r\n  transition: var(--transition-link);\r\n}\r\n\r\n.btn1__restart {\r\n  display: none;\r\n  justify-content: left;\r\n  align-items: center;\r\n}\r\n\r\n.btn1__restart h3 {\r\n  margin: 1.5rem 1.5rem 1.5rem 1.5rem;\r\n}\r\n\r\n.btn1__restart:hover .fa-redo-alt {\r\n  color: var(--color-piece2);\r\n  transition: var(--transition-link);\r\n}\r\n\r\n.btn1__pause {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: left;\r\n}\r\n\r\n.btn1__pause:hover .fa-pause {\r\n  color: var(--color-piece4);\r\n  transition: var(--transition-link);\r\n}\r\n\r\n.btn1__pause h3 {\r\n  margin: 1.5rem 1.5rem 1.5rem 1.5rem;\r\n}\r\n\r\n.btn2 {\r\n  justify-self: start;\r\n  align-self: start;\r\n  grid-area: btn2;\r\n  cursor: pointer;\r\n}\r\n\r\n.btn2__controls {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: left;\r\n}\r\n\r\n.btn2__controls:hover .fa-gamepad {\r\n  color: var(--color-piece6);\r\n  transition: var(--transition-link);\r\n}\r\n\r\n.btn2__controls h3 {\r\n  margin: 1.5rem 1.5rem 1.5rem 1.5rem;\r\n}\r\n\r\n.points-div {\r\n  cursor: auto;\r\n  margin-top: 10rem;\r\n}\r\n\r\n.points-div .points2-div {\r\n  margin: 2px 0;\r\n}\r\n\r\n.points1 {\r\n  color: var(--color-piece2);\r\n}\r\n\r\n.points2 {\r\n  color: var(--color-piece1);\r\n}\r\n\r\n#points1 {\r\n  color: var(--color-piece0);\r\n}\r\n\r\n#points2 {\r\n  color: var(--color-piece0);\r\n}\r\n\r\n.attacks-left1 {\r\n  justify-self: center;\r\n  align-self: start;\r\n  grid-area: attacks-left1;\r\n  margin: 0.5rem 0 0.5rem 0;\r\n}\r\n\r\n.attacks-left1 img {\r\n  width: 2rem;\r\n  height: 2rem;\r\n  margin-top: 10px;\r\n}\r\n\r\n.attacks {\r\n  font-size: 20px;\r\n  color: var(--color-piece3);\r\n}\r\n\r\n.attacks-left2 {\r\n  justify-self: center;\r\n  align-self: start;\r\n  grid-area: attacks-left2;\r\n  margin: 0.5rem 0 0.5rem 0;\r\n}\r\n\r\n.attacks-left2 img {\r\n  width: 2rem;\r\n  height: 2rem;\r\n  margin-top: 10px;\r\n}\r\n\r\n.score1 {\r\n  justify-self: center;\r\n  align-self: start;\r\n  grid-area: score1;\r\n}\r\n\r\n.score1__wins {\r\n  color: var(--color-piece2);\r\n}\r\n\r\n.score2 {\r\n  justify-self: center;\r\n  align-self: start;\r\n  grid-area: score2;\r\n}\r\n\r\n.score2__wins {\r\n  color: var(--color-piece1);\r\n}\r\n\r\n/* Player 1 Tetris Board */\r\n\r\n.player1-grid {\r\n  background: var(--color-background);\r\n  display: grid;\r\n  grid-template-columns: 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem;\r\n  grid-template-rows: 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem;\r\n  gap: 1px 1px;\r\n}\r\n\r\n.cell1 {\r\n  background: var(--color-main);\r\n  /* border-radius: 3px; */\r\n}\r\n\r\n/* Player 2 Tetris Board */\r\n\r\n.player2-grid {\r\n  display: grid;\r\n  background: var(--color-background);\r\n  grid-template-columns: 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem;\r\n  grid-template-rows: 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem 2.72rem;\r\n  gap: 1px 1px;\r\n}\r\n\r\n.cells {\r\n  background: var(--color-main);\r\n  border-radius: 2px;\r\n  border: none;\r\n  padding: 1px 2px;\r\n  /* box-shadow: 0px -1px 0px rgba(224, 224, 224, 0.3) inset; */\r\n  /* box-shadow: -1px 1.5px 1.5px 1px rgba(0, 0, 0, 0.05),\r\n    1px -1.1px 1.5px 1px rgba(255, 255, 255, 0.1); */\r\n}\r\n\r\n/* Icons */\r\n\r\n/* TODO: \r\n\r\nplace buttons into 1 merged button cell. add a restart button. with arrow in circle icon.\r\n\r\nadd Icon that \"!\" shows that you are being attacked. It's default color will be invisible or grey. will turn orange when being attacked. Need for both players\r\n\r\nborder radius on board pieces?\r\n\r\ndo I need to say start/pause/controls?\r\n*/"],"sourceRoot":""}]);
